                              <p>
                                  JSHSHIR  jismoniy shaxsning shaxsiy identifikasion raqami bolib, u har bir fuqaroning pasportida yoki ID kartasida yozilgan boladi.
                              </p>
                              <p>
                                  U 14 ta raqamdan iborat va ozgartirilishi mumkin emas.
                              </p>
                          </div>
                            <form @submit.prevent="putUsers()">

                                <div class="row w-100" >
